import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {Redirect} from "react-router-dom";

export const SET_AUTH_USER_DATA = 'SET-AUTH-USER-DATA'


let initialState = {
    userID: null,
    email: null,
    login: null,
    isAuth: false
}

let authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA: {
            return {
                ...state,
                ...action.data,
                isAuth: action.isAuth
            }
        }
        default:
            return state;
    }
}

export const setAuthUserData = (userID, email, login, isAuth) => ({
    type: SET_AUTH_USER_DATA,
    data: {userID, email, login},
    isAuth
})
export const setAuthUserThunkCreator = () => async (dispatch) => {
    {
        let response = await authAPI.setAuthorizedProfile()
        if (response.data.resultCode === 0) {
            let {id, login, email} = response.data.data;
            dispatch(setAuthUserData(id, email, login, true));
        }

    }
}

export const loginThunkCreator = (email, password, rememberMe) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe)
    if (response.data.resultCode === 0) dispatch(setAuthUserThunkCreator())
    else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error";
        dispatch(stopSubmit("login", {_error: message}));
    }

}
export const logoutThunkCreator = () => {
    return async (dispatch) => {
        let response = await authAPI.logout()
        dispatch(setAuthUserData(null, null, null, false))
    }
}

export default authReducer;